import React from "react";
import PropTypes from "prop-types";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Typography,
  withStyles
} from "@material-ui/core";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import ColoredButton from "../../../shared/components/ColoredButton";

const styles = theme => ({
  dialogActions: {
    justifyContent: "flex-start",
    paddingTop: theme.spacing(2),
    paddingBottom: theme.spacing(2),
    paddingRight: theme.spacing(2)
  },
  dialog: {
    zIndex: 1400
  },
  backIcon: {
    marginRight: theme.spacing(1)
  }
});

function CookieRulesDialog(props) {
  const { classes, onClose, open, theme } = props;
  return (
    <Dialog
      open={open}
      scroll="paper"
      onClose={onClose}
      className={classes.dialog}
    >
      <DialogTitle>Our Cookie Policy</DialogTitle>
      <DialogContent>
        <Typography variant="h6" color="primary" paragraph>
          Qu son las Cookies
        </Typography>
        <Typography paragraph>
          
          Como es prctica comn con casi todos los sitios web profesionales,
          este sitio utiliza cookies, que son archivos pequeos que se
          descargan en su ordenador, para mejorar su experiencia. Esta pgina
          describe qu informacin recogen, cmo la usamos y por qu a veces
          necesitamos almacenar estas cookies. Tambin compartiremos cmo puede
          evitar que estas cookies se almacenen, sin embargo, esto puede
          desactivar o &apos;romper&apos; ciertos elementos de la funcionalidad
          del sitio. Para obtener ms informacin general sobre las cookies, consulte el artculo de Wikipedia sobre HTTP Cookies.
        </Typography>
        <Typography variant="h6" color="primary" paragraph>
          Cmo usamos las cookies
        </Typography>
        <Typography paragraph>
          Usamos cookies por una variedad de razones detalladas a continuacin.
          Desafortunadamente, en la mayora de los casos no hay opciones
          estndar de la industria para deshabilitar las cookies sin
          deshabilitar completamente la funcionalidad y las caractersticas que
          aaden a este sitio. Se recomienda que deje todas las cookies si no
          est seguro de si necesita o no, ya que pueden ser utilizadas para
          proporcionar un servicio que utiliza.
        </Typography>
        <Typography variant="h6" color="primary" paragraph>
          Deshabilitando cookies
        </Typography>
        <Typography paragraph>
          Puede evitar la configuracin de cookies ajustando la configuracin de
          su navegador (consulte la Ayuda de su navegador para saber cmo hacer
          esto). Tenga en cuenta que la desactivacin de cookies afectar la
          funcionalidad de este y muchos otros sitios web que visita. La
          desactivacin de cookies generalmente tambin desactivar cierta
          funcionalidad y caractersticas de este sitio. Por lo tanto, se
          recomienda que no desactive las cookies.
        </Typography>
        <Typography variant="h6" color="primary" paragraph>
          Las cookies que establecemos
        </Typography>
        <Typography paragraph>Cookies relacionadas a la cuenta:</Typography>
        <Typography paragraph>
          Si crea una cuenta con nosotros, usaremos cookies para el manejo del
          proceso de registro y la administracin general. Estas cookies
          generalmente se eliminarn cuando cierre sesin, pero en algunos casos
          pueden permanecer despus para recordar sus preferencias de sitio
          cuando cierre sesin. Usamos cookies para recordar que acept este
          mensaje.
        </Typography>
  
      </DialogContent>
      <DialogActions className={classes.dialogActions}>
        <ColoredButton
          onClick={onClose}
          variant="contained"
          color={theme.palette.common.black}
        >
          Atrs
        </ColoredButton>
      </DialogActions>
    </Dialog>
  );
}

CookieRulesDialog.propTypes = {
  classes: PropTypes.object.isRequired,
  onClose: PropTypes.func.isRequired,
  open: PropTypes.bool.isRequired,
  theme: PropTypes.object.isRequired
};

export default withStyles(styles, { withTheme: true })(CookieRulesDialog);
